
p {
    padding: 0;
    margin: 0;
}
.msAuditingIng {
    padding: 15px;
}
.msAuditing-title {
    border-bottom: 1px solid #ccc;
}
.msAuditing-title i {
    display: block;
    width: 3px;
    height: 20px;
    background-color: #6699cc;
    position: absolute;
    top: 35px;
    left: 10px;
}
.msAudtingIng-content {
    padding: 20px 15px 0 15px;
    font-size: 14px;
}
.msAudtingIng-content > div {
    margin-bottom: 15px;
}
.msAudtingPerson i {
    width: 27px;
    height: 35px;
    display: inline-block;
    font-size: 35px;
    vertical-align: middle;
    margin-right: 20px;
}
.ms-aud-left {
    width: 30%;
}
.ms-aud-right{
    width: 70%;
}
.red {
    color: red;
}
.msAudtingIngReason textarea {
    width: 100%;
    height: 150px;
    padding: 10px;
}
.msAudtingIng-submit {
    text-align: center;
}
.msAudtingIng-submit a {
    width: 30%;
    color: #fff;
    display: inline-block;
    margin-right: 20px;
}
.msAudtingIng-submit button {
    width: 30%;
    color: #fff;
}
.msAudtingIng-submit a button {
    width: 100%;
    color: #fff;
}
.msAudtingIngPass {
    margin-right: 20px;
    background-color: #6699cc;
}
.msAudtingIngFail {
    background-color: rgb(215,215,215);
}



