
@import "../golbal/global.css";
ul#left-menu{
  padding: 15px;
}
#left-menu .item{
  line-height: 54px;
}
#left-menu .item button{
  border:none;
  width: 100%;
  text-align: left;
  padding: 0px!important;
  line-height: 54px;
}
#setting{
  color: #007aff;
  border:none;
  background: transparent;
}
#header-icon{
  height: 40px;
  width: 40px;
}
.photoImg {
  width: 65px;
  height: 65px;
  background-color: #6699cc;
  overflow: hidden;
}
.photoImg #header-icon {
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 50%;
  vertical-align: middle;
}



