
@import '../../golbal/global.css';
@import '../../../static/svgIcon/iconfont.css';
h3, p {
    padding: 0;
    margin: 0;
}
.replyMessage {
    padding: 15px;
}
.replyMessage-search{
}
.replyMessage-search input {
    width: 80%;
    border: 1px solid #ccc;
    border-radius: 10px;
    height: 35px;
    margin-right: 10px;
}
.replyMessage-search i {
    font-size: 20px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    display: inline-block;
    border: 1px solid #ccc;
    vertical-align: middle;
    border-radius: 50%;
    background-color: #999;
    color: #fff;
}
.replyMessage-main {
    margin-top: 20px;
}
.replyMessage-main h3 {
    font-size: 16px;
    border-bottom: 1px solid #ccc;
    line-height: 35px;
    height: 35px;
}
.replyMessage-content li {
    padding: 10px;
    border-bottom: 1px solid #ccc;
}
.content-top {
    font-size: 14px;
    color: #8f8f94;
    text-align: center;
}
.replyAll {
    margin-top: 30px;
}
.replyChange {
    color:blue;
}
.replyChange button {
    background-color: rgb(102,153,204);
    color: #fff;
}




