}
#fun-list{
    box-sizing: border-box;
}
#fun-list li{
    width: 33%;
    text-align: center;
    float: left;
}
#lately-week-send-list li{
    padding-left: 115px;
    position: relative;
}
#lately-week-send-list .list-img{
    position: absolute;
    top:0;
    left: 0;
    display: block;
    width: 100px;
    height: 75px;
}




