
@import '../../golbal/global.css';
body {
        background-color: #fff;
}
h3,p {
        padding: 0;
        margin: 0;
}
.accountSetting {
    /*padding-top: 30px;*/
    color: #666;
}
.accountSetting-title {
        font-weight: 600;
        font-size: 16px;
        height: 40px;
        line-height: 40px;
        background-color: rgb(242,242,242);
        padding-left: 15px;
        color: #666;
}
.accoutSetting-main {
        padding: 30px 15px 0 15px;
}
.setting-pic {
        width: 65px;
        height: 65px;
        background-color: #6699cc;
        overflow: hidden;
}
.setting-pic img {
        width: 100%;
        height: 100%;
        border: 0;
        border-radius: 50%;
        vertical-align: middle;
}
.settings {
        height: 65px;
        line-height: 65px;
        margin-left: 40px;
}
.accountSetting-userMsg{
        padding-top: 30px;
}
.accountSetting-userMsg li {
        border-bottom: 1px solid #ccc;
        padding: 10px;
}
.accountSetting-userMsg input {
        float: left;
        width: 60%;
        margin: 0;
        padding: 0;
        border:0;
        font-size: 16px;
}
.userMsg-left {
        display: inline-block;
        width: 30%;
        margin-right: 10px;
        float: left;
        height: 100%;
        line-height: 40px;
}
.accoutSetting-main a {
        display: block;
        width: 70%;
        margin: 0 auto;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin-top: 30px;
}
.accoutSetting-main button {
        border: 0;
        background-color: #6699cc;
        color:#fff;
        display: block;
        width: 70%;
        margin: 0 auto;
        height: 40px;
        text-align: center;
        max-width: 300px;
        margin-top: 30px;
}



