
@import '../../golbal/global.css';
@import '../../../static/svgIcon/iconfont.css';
p {
    padding: 0;
    margin: 0;
}
.msAuditing {
    padding: 15px;
}
.msAuditing-title {
    border-bottom: 1px solid #ccc;
}
.msAuditing-title i {
    display: block;
    width: 3px;
    height: 20px;
    background-color: #6699cc;
    position: absolute;
    top: 35px;
    left: 10px;
}
.msAuditing-content {
}
.msAuditing-content li {
    border-bottom: 1px solid #ccc;
    position: relative;
    padding: 10px 0;
}
.msAuditing-content i {
    width: 24px;
    height: 24px;
    display: inline-block;
    position: absolute;
    font-size: 24px;
    vertical-align: middle;
    top: 50%;
    margin-top: -12px;
}
.ms-con-message {
    float: left;
    width: 35%;
    margin-left: 35px;
    margin-right: 15px;
}
.ms-con-source {
    width: 35%;
}
.msAuditing-content b {
    width: 20px;
    height: 20px;
    font-size: 20px;
    position: absolute;
    top: 50%;
    color: deeppink;
    margin-top: -10px;
    right: 10px;
}
.red {
    color: red;
}



