
#title{
    border-bottom:1px solid #d9d9d9;
}
.form-item-row{
    padding-left: 75px;
    position: relative;
}
.item-row-title{
    position: absolute;
    top:0;
    left: 0;
}
.picker-item input{
    /*border:none;*/
    /*border-bottom: 1px solid #d9d9d9;*/
    /*line-height: 34px;*/
    /*height:34px ;*/
}
input[name=checkbox1]:before{
    font-size: 20px!important;
}
input[name=checkbox1]{
    /*top: 0!important;*/
    right:0!important;
    left: 0!important;
}
.mui-checkbox.mui-left label, .mui-radio.mui-left label{
    padding-left: 25px!important;
}
.mui-input-row label{
    padding:0px 15px!important;
}
#rightLeft{
    position: fixed;
    top:0;
    left: 100%;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #fff;
}
#rightLeft:before{
    content: "";
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.right-to-left{
    animation: moveLeftToRight .3s linear;
    animation-iteration-count:1;
    animation-fill-mode:forwards;
}
@keyframes moveLeftToRight {
0%  {left:100%
}
100% {left:0%
}
}
@-webkit-keyframes moveLeftToRight {
from {left:100%
}
to {left:0%
}
}
.mui-indexed-list-inner .mui-scroll-wrapper{
    top:35px!important;
}



