
@import '../../golbal/global.css';
@import '../../../static/svgIcon/iconfont.css';
p {
    padding: 0;
    margin: 0;
}
.msAuditingdefault {
    padding: 15px;
    text-align: center;
}
.msAuditing-title {
    border-bottom: 1px solid #ccc;
    text-align: left;
}
.msAuditing-title i {
    display: block;
    width: 3px;
    height: 20px;
    background-color: #6699cc;
    position: absolute;
    top: 35px;
    left: 10px;
}
.default {
    padding-top: 100px;
    text-align: center;
    font-size: 18px;
    color: red;
}
.msAuditingdefault .default i {
    width: 30px;
    height: 30px;
    font-size: 30px;
    margin-right: 40px;
    vertical-align: middle;
    color: red;
}
.aud-return {
    width: 100px;
    height: 35px;
    text-align: center;
    background-color: #6699cc;
    color:#fff;
    margin-top: 50px;
}



