
@import "../../golbal/global.css";
.usercpm {
    padding: 30px;
    position: relative;
}
.user-login {
    /*height: 150px;*/
    /*padding: 0 10px;*/
}
.user-login .userlogin-name ,  .user-login .userlogin-psw{
    padding-left: 35px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid #6699cc;
}
.user-login-btn {
    padding: 0;
    display: block;
    width: 250px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 18px;
    margin: 0 auto;
    background-color: #6699cc;
    border: 0;
    color: #fff;
    border-radius: 10px;
    margin-top: 30px;
    max-width: 200px;
}
.user-login-btn a {
    display: block;
    width: 100%;
    height: 100%;
    color: #fff;
    text-decoration: none;
}
.login-er {
    /*position: absolute;*/
    /*right: 20px;*/
    /*top: 5px;*/
    width: 46px;
    height: 46px;
    line-height: 46px;
    font-size: 46px;
}



